var dataType= {
  number:
  {
    reg: /^[0-9]*$/,
      input_decs: ""
  },
  string: {
    reg: /[\\s\\S]*?/,
      input_decs: ""
  }
};
export default {
  notNull: /\S/,
  dataType: dataType,
  comData: {
    normal: [{
      value: true,
      key: ""
    },
    {
      value: false,
      key: ""
    }]
  }
  ,
  keybordCode: {
    Up: 38,
    Down: 40,
    Enter: 13

  },
  fn: [
    { view: "=", parm: [{ type: dataType.string }], tip: "" },
    { view: ">", parm: [{ type: dataType.number }], tip: "" },
    { view: "<", parm: [{ type: dataType.number }], tip: "" },
    { view: ">=", parm: [{ type: dataType.number }], tip: "" },
    { view: "<=", parm: [{ type: dataType.number }], tip: "" },
    { view: "<>", parm: [{ type: dataType.string }], tip: "" },
    { view: "", parm: [{ type: dataType.string }], tip: "" },
    { view: "", parm: [{ type: dataType.string }], tip: "" }
  ],
  nodeType: {
    fn: "fuction",
    col: "column",
    parm: "parm"
  }
}



