<template>
  <div class="right-top">
    <div class="col_info">

      <div class="title">
        <transition name="fade">
          <div class="now_column">{{select_object.selected_text}} </div>

          </transition>
          <ul class="tabs"><li v-bind:class="{active:select_object.all_col}"></li><li v-bind:class="{active:select_object.all_row}"></li><li v-bind:class="{active:select_object.normal}"></li></ul>
      </div>
      <div class="selectBox">

        <input placeholder=" ..." type="text" />
      </div>
    </div>
    <div class="function_group">
      <div v-for="(item,index) in  grouplist">
        <div  class="function_group_title">{{item.group_name}}</div>
        <ul class="function suggestion">
          <li v-for="(child,index) in item.fn_list" v-bind:data-name="child.fn_name">
            <Function v-bind:function_data="child" v-bind:operator_data="select_object" v-bind:operator_name="select_object.selected_text"/>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
  import '../assets/js/global/global_drag.js'
  import loading_gif from '../resourse/968f5530a66a4124366500c7de835816.gif'
  import Function from './part/function'

  //
  var data = [{
    group_name: "", fn_list: [{
      fn_name: "test",
      fn_label: "",
      parm:
        [
          //1
          {
            parm_name: //,
              "name",
            parm_type: //,data
              "number",
            components://
              {
                tag: "input",
                type: "text",//select,textarea......
                value: null,//
                label: "",//
                placeholder: "",//,
                data:[],//,key-value
                desc: ""   //
              }
          }

          //N.....

        ]
    },
    {
      fn_name: "test",
      fn_label: "2",
      parm:
        [
          //1
          {
            parm_name: //,
              "name",
            parm_type: //,data
              "string",
            components://
              {
                tag: "select",
                // type: "text",//select,textarea......
                //value: null,//
                label: "",//
                //  placeholder: "",//,
                value: true,
                data: "normal",//,key-value
                desc: ""   //
              }
          }
          ,
          {
            parm_name: //,
              "desc",
            parm_type: //,data
              "string",
            components://
              {
                tag: "textarea",
                value: null,//
                label: "",//
                placeholder: "",//,
                data: [],//,key-value
                desc: ""   //
              }
          }
          //N.....

        ]
    },
    {
      fn_name: "test",
      fn_label: "3",
      parm:
        [
          //1
          {
            parm_name: //,
              "name",
            parm_type: //,data
              "string",
            components://
              {
                tag: "select",
                // type: "text",//select,textarea......
                //value: null,//
                label: "",//
                //  placeholder: "",//,
                data: [{ key: "124", value: "456" }],//,key-value
                desc: ""   //
              }
          },
          //1
          {
            parm_name: //,
              "name",
            parm_type: //,data
              "string",
            components://
              {
                tag: "select",
                // type: "text",//select,textarea......
                //value: null,//
                label: "",//
                //  placeholder: "",//,
                data: [{ key: "124", value: "456" }],//,key-value
                desc: ""   //
              }
          }

          //N.....

        ]
    },
    {
      fn_name: "test",
      fn_label: "4",
      parm:
        [
          //1
          {
            parm_name: //,
              "name",
            parm_type: //,data
              "string",
            components://
              {
                tag: "select",
                // type: "text",//select,textarea......
                //value: null,//
                label: "",//
                //  placeholder: "",//,
                data: [{ key: "124", value: "456" }],//,key-value
                desc: ""   //
              }
          }

          //N.....

        ]
    }]
  }];
  export default {
    components: { Function},
    mounted: function () {
      var _super = this;
      window.Bus.$watch('gridSelected', function (newValue, oldValue) {


        this.select_object = newValue;
        this.refesh();
      }.bind(this));
      this.function_container =$(".function_group");
      $Drag.enableDrag(".function_group", ".label");
      this.function_container.delegate(".label", "click", function () {
    
        $(this).parent().find(".content").slideToggle(200);
        _super.function_container.animate({ scrollTop: $(this).offset().top - _super.function_container.offset().top + _super.function_container.scrollTop() }, 200);
      });

    },
    data: function () {
      return {
        grouplist: [],
        select_object: {}
      }
    },
    methods: {
      loading: function () {
        this.function_container.css("background-image", "url('" + loading_gif + "')");
      },
      hideLoading: function () {
        this.function_container.css("background-image", "");
      },
      refesh: function () {

        var _super = this;
        this.grouplist = [];
        this.loading();
        setTimeout(function () {
   
          this.grouplist = data;
          this.hideLoading();
        }.bind(this), 1500);
      
      }
    },
    name: "FunctionList"
  }
</script>



